import React from 'react'
import classnames from 'classnames'
import { getPokemonDefaultFrontSpriteUrl } from './../../../api/PokeApi'

const PokemonBoardItemImage = ({ className, pokemonId, openModal }) => {
  const imageUrl = getPokemonDefaultFrontSpriteUrl(pokemonId)

  const onClick = event => {
    event.preventDefault()

    openModal && openModal()
  }

  return (
    <a
      className={classnames('pokemon-board-item-image', className)}
      style={{ backgroundImage: `url("${imageUrl}")` }}
      onClick={onClick}
      href="/pokemon"
    >
    </a>
  )
}

export default PokemonBoardItemImage
