import React from 'react'
import numeral from 'numeral'
import { capitalize } from './../../../../utils/stringFormats'

const Pokemon = ({ pokemon }) => {
  if (!pokemon) return null

  const renderPokemonTypes = types => {
    return (
      <div className="type-list">
        {types.map(t => (
          <div className="type" key={t.id}>
            {capitalize(t.name)}
          </div>
        ))}
      </div>
    )
  }

  return (
    <div className="pokemon">
      <div
        className="pokemon-image"
        style={{ backgroundImage: `url(${pokemon.image})` }}
      />
      <div className="pokemon-number">
        {'#' + numeral(pokemon.id).format('000')}
      </div>
      <div className="pokemon-name">{pokemon.pokedex.name}</div>
      <div className="pokemon-types">
        {renderPokemonTypes(pokemon.pokedex.types)}
      </div>
    </div>
  )
}

export default Pokemon
